/* Full-screen background with a centered login form */
.container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-image: url('https://yourimageurl.com/clothing-background.jpg'); /* Replace with your desired image */
    background-size: cover;
    background-position: center;
    background-attachment: fixed; /* Keeps the background fixed while scrolling */
    position: relative;
  }
  
  /* Dark transparent overlay to make the form readable against the background */
  .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5); /* Dark semi-transparent background */
  }
  
  /* Styling for the login card */
  .card {
    position: relative;
    width: 100%;
    max-width: 400px;
    background-color: rgba(255, 255, 255, 0.9); /* Slightly transparent white for the form */
    padding: 40px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
    border-radius: 15px;
    z-index: 10;
    backdrop-filter: blur(10px); /* Adds a nice blur effect to the background */
  }
  
  /* Styling for the heading */
  .heading {
    text-align: center;
    color: #333;
    font-size: 28px;
    font-weight: 600;
    margin-bottom: 20px;
    font-family: 'Arial', sans-serif;
  }
  
  /* Styling the form */
  .form {
    display: flex;
    flex-direction: column;
  }
  
  /* Input group styling */
  .inputGroup {
    margin-bottom: 20px;
  }
  
  /* Label styling */
  .label {
    font-size: 16px;
    color: #333;
    margin-bottom: 5px;
  }
  
  /* Input field styling */
  .input {
    padding: 15px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 8px;
    outline: none;
    transition: border-color 0.3s ease;
  }
  
  .input:focus {
    border-color: #007bff;
  }
  
  /* Submit button styling */
  .submitButton {
    padding: 15px;
    font-size: 16px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .submitButton:hover {
    background-color: #0056b3;
  }
  
  /* Styling for the sign-up prompt text */
  .signupPrompt {
    text-align: center;
    margin-top: 20px;
    color: #666;
  }
  
  /* Link styling */
  .link {
    color: #007bff;
    text-decoration: none;
  }
  
  .link:hover {
    text-decoration: underline;
  }
  